
//-------------------- Base Typography --------------------//

$cx-body-font: 					'Poppins', sans-serif !default;


//-------------------- Font Sizes --------------------//

$cx-body-font-size: 			13px !default;
$cx-h1-font-size: 				22px !default;
$cx-h2-font-size: 				20px !default;
$cx-h3-font-size: 				18px !default;
$cx-h4-font-size: 				15px !default;
$cx-h5-font-size: 				12px !default;
$cx-h6-font-size: 				10.2px !default;
$cx-icon-font-size: 			14px !default;


//-------------------- Base Color --------------------//

$cx-text-color: 				#4d4d4d !default;
$cx-body-bg-color: 				#fff !default;

$cx-primary-color-1:  			#ec610a !default;
$cx-secondary-color-1: 			#292929 !default;
// $cx-tertiary-color:				#3e3e3e !default;
// $cx-fourth-color: 				#ffcb5e !default;
// $cx-grey-color: 				#9aabb8 !default;

$cx-offset-color:				#f7f7f7 !default;
$cx-border-color:				#ededed !default;


//-------------------- Color Palletes --------------------//

$scheme: (
    pallete-one: (
        primary:		#ec610a,
        secondary:		#ec610a,
        header:			#fff
    ),
    pallete-two: (
        primary:		#ffc300,
        secondary:		#ffc300,
        header:			rgba(255, 255, 255, 0)
    ),
    pallete-three: (
        primary:		#2b7fd0,
        secondary:		#f8ce31,
        header:			#2b7fd0
    ),
    pallete-four: (
        primary:		#d13636,
        secondary:		#f9ce31,
        header:			#cf2c2c
    )
);


//-------------------- Line Height --------------------//

$cx-body-line-height: 			20px !default;
$cx-body-font-weight : 			400 !default;


//-------------------- Default Spacing --------------------//

$cx-heading-margin-bottom: 		10px !default;
$cx-section-spacing:	 		60px !default;
$cx-col-spacing:				0.5 * $cx-section-spacing !default; // will be used as bottom margin/padding


//-------------------- Default Mobile Spacing --------------------//

$shrink-factor:					0.75 !default; // shrink spacing for mobile
$cx-col-spacing-mob:			$shrink-factor * $cx-col-spacing !default; // shrink col spacing for mobile
$cx-section-spacing-mob:		$shrink-factor * $cx-section-spacing !default; // shrink col spacing for mobile


//-------------------- Default Transition --------------------//

$cx-transition: 				 all 0.3s ease-in-out !default;


//-------------------- Default Breakpoint / Screen Size --------------------//

$cx-lg-screen:					1200px !default;
$cx-md-screen: 					992px !default;
$cx-sm-screen:					768px !default;
$cx-vsm-screen:					480px !default;

$cx-breakpoints: (
    "wide":    					$cx-lg-screen,
    "notebook":    				$cx-md-screen,
    "tablet":    				$cx-sm-screen,
    "small-mobile": 			$cx-vsm-screen, 
);


//-------------------- Social Icon Colors --------------------//

$facebook-color:           		#435f9f;
$twitter-color:                 #00aaf0;
$gplus-color:                   #e04b34;
$pinterest-color:               #ce1f21;
