/* Border-radius */

.rounded-corners {
	@extend %rounded-corners;
}

.circle {
	@include border-radius(50%);
}

/* Equal height by display: table */

.eqheight {
	@extend %eqheight;
}


/* Overflow */

.oflow-hidden {
	@extend %overflow-hidden;
}

/* Position */

.pos-r {
	position: relative;
}

.pos-a {
	position: absolute;
}

/* Tooltips */

.tooltip {
	z-index: 9950;
	.arrow{
		&::before {
			border-top-color: $cx-secondary-color-1;
		}
	}

	.tooltip-inner {
		@extend %bgc-secondary-1;
	}
}


